import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { PageHeaderComponent, CounterComponent, UserImageComponent } from './components';

const MODULES = [
  CommonModule,
  HttpModule,
  FormsModule,
  NgbModule,
];

@NgModule({
  imports: [CommonModule, FormsModule],
  declarations: [PageHeaderComponent, CounterComponent, UserImageComponent],
  exports: [
    ...MODULES,
    PageHeaderComponent, CounterComponent, UserImageComponent
  ],
})
export class SharedModule {
  static forRoot() {
    return [
      NgbModule.forRoot()
    ]
  }
}



